.nav-bottom {
  width: 100%;
  max-width: 1110px;
  margin: 150px auto 280px;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  @media (max-width: 767px) {
    flex-direction: column;
    margin: 50px auto 130px;
    align-items: center;
  }
  a {
    width: 100%;
    text-align: center;
    @media (max-width: 767px) {
      max-width: none;
      width: 80%;
      margin: 10px 0;
    }
    &.disable {
      cursor: default;
      pointer-events: none;
      background-color: #fff;
      color: #cbd5db !important;
    }
  }
}



