.title-page {
  text-align: center;
  margin-top: 85px;
  margin-bottom: 110px;
  @media (max-width: 767px) {
    margin-top: 65px;
    margin-bottom: 75px;
  }
  h2 {
    color: #030077;
    font-size: 103px;
    font-weight: 700;
    line-height: 95px;
    margin-bottom: 20px;
    @media (max-width: 1199px) {
      font-size: 90px;
    }
    @media (max-width: 991px) {
      font-size: 80px;
      line-height: 80px;
      text-align: center;
    }
    @media (max-width: 767px) {
      font-size: 60px;
      line-height: 60px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
      line-height: 45px;
    }
  }
  h4 {
    margin-top: 40px;
    color: #d11742;
    font-size: 70px;
    font-weight: 700;
    line-height: 85px;
    @media (max-width: 1199px) {
      font-size: 77px;
      line-height: 77px;
    }
    @media (max-width: 991px) {
      font-size: 67px;
      line-height: 67px;
    }
    @media (max-width: 767px) {
      font-size: 47px;
      line-height: 47px;
    }
    @media (max-width: 575px) {
      font-size: 28px;
      line-height: 28px;
    }
    span {
      text-transform: uppercase;
    }
  }
}



