.slider-box {
  max-width: 100%;
  width: 100%;
  height: 940px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right top;
  background-position-y: -110px;
  @media (max-width: 1750px) {
    background-position-y: -55px;
  }
  @media (max-width: 1675px) {
    background-position-y: -75px;
    background-size: 120%;
  }
  @media (max-width: 1400px) {
    background-position-y: -80px;
    background-size: auto 110%;
  }
  @media (max-width: 1200px) {
    max-height: 730px;
    background-size: auto 115%;
    background-position-x: -190px;
  }
  @media (max-width: 1090px) {
    background-position-x: -250px;
  }
  @media (max-width: 991px) {
    max-height: 640px;
    background-position-x: -150px;
    background-size: 130%;
    background-position-y: -20px;
  }
  @media (max-width: 850px) {
    background-size: 140%;
    background-position-y: -9px;
    background-position-x: -230px;
  }
  @media (max-width: 767px) {
    max-height: 450px;
    background-size: 130%;
    background-position: -119px 0;
  }
  @media (max-width: 575px) {
    max-height: 500px;
    background-size: 1080px;
    background-position: calc(100% + 180px) -150px;
    .full-container {
      margin-top: 0;
      flex-direction: column-reverse;
    }
    .slider-box__img {
      max-width: 230px;
      margin-left: auto;
      img {
        margin-left: auto;
        max-width: 340px;
        width: 100%;
      }
    }
    .slider-box__text {
      padding: 0 15px;
    }
  }
  @media (max-width: 420px) {
    .slider-box__text {
      margin-top: -15px;
    }
  }
  &:nth-child(2) {
    background-size: 115%;
    background-position-x: -35px;
    background-position-y: -535px;
    @media (max-width: 2200px) {
      background-position-x: -140px;
      background-position-y: -400px;
    }
    @media (max-width: 1800px) {
      background-position-x: -150px;
      background-position-y: -255px;
    }
    @media (max-width: 1565px) {
      background-position-x: -265px;
      background-size: 130%;
      background-position-y: -225px;
    }
    @media (max-width: 1350px) {
      background-position-x: -465px;
      background-size: 150%;
      background-position-y: -225px;
    }
    @media (max-width: 1200px) {
      background-position-x: -265px;
      background-position-y: -205px;
    }
    @media (max-width: 1080px) {
      background-position-y: -100px;
    }
    @media (max-width: 820px) {
      background-position-y: -100px;
    }
    @media (max-width: 767px) {
      background-position-y: -120px;
      background-position-x: -140px;
      .slider-box__text p {
        font-size: 15px !important;
        line-height: 20px !important;
      }
    }
    @media (max-width: 575px) {
      background-size: 1100px;
      background-position: calc(100% + 235px) -230px;
      .slider-box__text h2 {
        margin-bottom: 10px;
      }
    }
  }
  &:nth-child(3) {
    .full-container {
      @media (min-width: 1201px) {
        margin-top: 250px;
      }
    }
    @media (max-width: 991px) {
      .full-container {
        flex-direction: column;
      }
      .slider-box__text {
        max-width: 610px;
        padding-right: 15px;
        padding-left: 15px;
      }
      .slider-box__text p {
        font-size: 22px !important;
        line-height: 22px !important;
        text-align: center;
      }
      .slider-box__img {
        margin-top: 10px;
        margin-left: 0 !important;
        text-align: center;
      }
      .slider-box__img h3 {
        font-size: 25px;
        line-height: 25px;
        text-align: center;
      }
      .slider-box__img h4 {
        font-size: 20px;
        line-height: 20px;
        text-align: center;
      }
    }
    @media (max-width: 767px) {
      .full-container {
        margin-top: 17px;
      }
      .slider-box__text {
        max-width: 450px;
      }
      .slider-box__text p {
        font-size: 15px !important;
        line-height: 10px !important;
      }
      .slider-box__img h3 {
        font-size: 25px;
        line-height: 25px;
      }
      .slider-box__img h4 {
        font-size: 20px;
        line-height: 20px;
      }
    }
    @media (max-width: 575px) {
      .full-container {
        margin-top: 60px;
        flex-direction: column;
      }
      .slider-box__text {
        max-width: 450px;
        padding-right: 15px;
        padding-left: 15px;
      }
      .slider-box__text p {
        font-size: 15px !important;
        line-height: 15px !important;
      }
      .slider-box__img {
        max-width: 100%;
        margin-left: 0;
        padding: 0 15px;
      }
      .slider-box__img h3 {
        font-size: 25px;
        line-height: 25px;
      }
      .slider-box__img h4 {
        font-size: 20px;
        line-height: 20px;
        margin: 15px 0;
      }
      .slider-box__img p {
        line-height: 20px !important;
      }
    }
  }
  .full-container {
    max-width: 100%;
    padding: 0 265px;
    margin-top: 140px;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1500px) {
      padding: 0 170px;
    }
    @media (max-width: 1200px) {
      padding: 0 60px;
    }
    @media (max-width: 991px) {
      padding: 0 0;
      margin-top: 100px;
    }
    @media (max-width: 767px) {
      margin-top: 60px;
    }
    @media (max-width: 575px) {
      margin-top: 0;
    }
  }
  &__text {
    max-width: 740px;
    @media (max-width: 1200px) {
      padding-left: 40px;
    }
    @media (max-width: 485px) {
      h2 {
        font-size: 20px !important;
      }
      strong {
        font-size: 16px !important;
        line-height: 20px !important;
      }
    }
    .video {
      position: relative;
      video {
        max-height: 395px;
      }
      &.pause {
        &:before {
          opacity: 0;
        }
      }
      &:before {
        content: url("images/play.svg");
        width: 95px;
        height: 95px;
        position: absolute;
        margin: auto;
        z-index: 9;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 1;
        pointer-events: none;
      }
    }
    h2 {
      display: block;
      margin-bottom: 40px;
      font-size: 51px;
      color: white;
      font-weight: 800;
      @media (max-width: 991px) {
        margin-bottom: 30px;
        font-size: 37px;
      }
      @media (max-width: 767px) {
        margin-bottom: 25px;
        font-size: 30px;
      }
      @media (max-width: 575px) {
        margin-bottom: 20px;
        font-size: 25px;
      }
    }
    strong {
      font-size: 30px;
      font-weight: 700;
      color: white;
    }
    p {
      max-width: 735px;
      font-size: 25px;
      font-weight: 400;
      color: white;
      @media (max-width: 991px) {
        font-size: 25px;
      }
      @media (max-width: 767px) {
        font-size: 20px;
      }
      @media (max-width: 575px) {
        font-size: 15px;
      }
    }
  }
  &__img {
    max-width: 540px;
    img {
      width: 100%;
    }
    h3 {
      color: #d11742;
      font-size: 65px;
      font-weight: 700;
      line-height: 65px;
    }
    h4 {
      color: #030077;
      font-size: 40px;
      font-weight: 800;
      line-height: 45px;
      text-transform: uppercase;
      display: block;
      margin: 15px 0;
    }
    p {
      color: #030077;
      font-size: 25px;
      font-weight: 400;
      line-height: 25px;
    }
  }
}



