.button--blue {
  max-width: 226px;
  height: 80px;
  border-radius: 67px;
  background-color: #030077;
  color: #fff !important;
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  margin: auto;
  @include flex-center;
  &:hover {
    animation-name: click;
    animation-timing-function: linear;
    animation-duration: 0.3s;
  }
}
.button--red {
  max-width: 226px;
  height: 80px;
  border-radius: 67px;
  background-color: #d11742;
  color: #fff !important;
  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  margin: auto;
  text-transform: uppercase;
  @include flex-center;
  &:hover {
    animation-name: click;
    animation-timing-function: linear;
    animation-duration: 0.3s;
  }
}
@keyframes click {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}



