.simptome {
  .title {
    margin-top: 95px;
    p {
      text-align: left;
      max-width: 800px;
      margin: 90px auto 0;
      color: #030077;
      font-size: 28px;
      font-weight: 700;
      line-height: 50px;
      text-transform: uppercase;
    }
  }
  .fixed-box {
    margin-bottom: 300px;
  }
  .footer {
    background: white;
    .logo-white {
      display: none;
    }
    .logo-black {
      display: block;
    }
  }
  .line-anim {
    max-width: 1088px;
    margin: 65px auto 0;
    overflow: hidden;
    position: relative;
    .slick-arrow {
      display: none !important;
    }
    &__all {
      display: flex;
      position: absolute;
      z-index: 9;
      right: -100%;
    }
    &__line {
      margin-top: 200px;
      position: relative;
      width: 100%;
      @media (max-width: 1099px) {
        margin-top: 140px;
      }
      @media (max-width: 767px) {
        margin-top: 110px;
        &:before,
        &:after {
          content: none !important;
        }
      }
      @media (max-width: 470px) {
        margin-top: 70px;
      }
      &:before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        width: 291px;
        top: 0;
        height: 29px;
        transition: 0.5s;
        transition-delay: 0.3s;
        background-color: #cbd5db;
      }
      &:after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        width: 291px;
        bottom: 0;
        height: 29px;
        background-color: #cbd5db;
        transition: 0.5s;
        transition-delay: 0.3s;
      }
    }
    &__scroll {
      padding-top: 70px;
      max-width: 1020px;
      display: flex;
      position: absolute;
      top: 0;
      right: 40%;
      transition: 0.6s;
      @media (max-width: 1199px) {
        max-width: 1010px;
      }
      @media (max-width: 991px) {
        max-width: 995px;
      }
      @media (max-width: 767px) {
        max-width: 985px;
      }
    }
    &__item {
      max-width: 105px;
      margin: 0 32px;
      transition: 0.3s;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 767px) {
        margin: 0 10px;
        max-width: none;
        img {
          width: 60px !important;
        }
      }
      @media (max-width: 470px) {
        img {
          width: 40px !important;
        }
      }
      img {
        width: 100%;
      }
    }
    &__text {
      @media (max-width: 767px) {
        margin: 0 !important;
      }
    }
    &__cordin {
      overflow: hidden;
      margin-top: -150px;
      padding-top: 190px;
      height: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      outline: none;
      position: relative;
      @media (max-width: 991px) {
        margin-left: -5px;
      }
      &:first-child {
        .line-anim__text {
          margin-left: 280px;
        }
      }
      &:nth-child(2) {
        .line-anim__text {
          max-width: 340px;
          margin-left: 310px;
        }
      }
      &:nth-child(4) {
        .line-anim__text {
          max-width: 340px;
          margin-left: 170px;
        }
      }
      &:last-child {
        .line-anim__text {
          text-align: center;
        }
      }
      &.slick-active {
        .line-anim__text {
          span,
          p {
            transform: scale(1);
            margin-top: 10px;
          }
        }
        &:first-child {
          .line-anim__all {
            right: 32%;
            transition: 1s;
          }
        }
        &:nth-child(2) {
          .line-anim__all {
            right: 32%;
            transition: 1s;
          }
        }
        &:nth-child(3) {
          .line-anim__all {
            right: 43%;
            transition: 1s;
          }
        }
        &:nth-child(4) {
          .line-anim__all {
            right: 43%;
            transition: 1s;
          }
        }
        &:nth-child(5) {
          .line-anim__all {
            right: 47%;
            transition: 1s;
          }
        }
        .line-anim__item {
          &:first-child {
            animation-name: first;
            animation-delay: 0.5s;
            animation-duration: 1s;
          }
          &:nth-child(2) {
            animation-name: first;
            animation-delay: 0.7s;
            animation-duration: 1s;
          }
          &:nth-child(3) {
            animation-name: first;
            animation-delay: 0.9s;
            animation-duration: 1s;
          }
          &:last-of-type {
            animation-name: end !important;
            animation-delay: 1.1s;
            animation-duration: 1s;
            transform: scale(1.3) !important;
          }
        }
        .line-anim__line {
          &:before {
            height: 0;
          }
          &:after {
            height: 0;
          }
        }
      }
      @keyframes first {
        0% {
          transform: scale(1);
        }
        50% {
          transform: scale(1.25);
        }
        100% {
          transform: scale(1);
        }
      }
      @keyframes end {
        0% {
          transform: scale(1.3);
        }
        50% {
          transform: scale(1.4);
        }
        100% {
          transform: scale(1.3);
        }
      }
      img {
        width: 100%;
        min-width: 105px;
        @media (max-width: 991px) {
          min-width: 80px;
        }
        @media (max-width: 767px) {
          min-width: 40px;
        }
      }
      span {
        display: block;
        margin-top: 15px;
        color: #d11742;
        font-size: 54px;
        font-weight: 700;
        line-height: 64.59px;
        transform: scale(0.8);
        transition: 0.5s;
        transition-delay: 0.3s;
        @media (max-width: 1099px) {
          font-size: 40px;
        }
        @media (max-width: 470px) {
          font-size: 30px;
        }
      }
      p {
        color: #030077;
        font-size: 25px;
        font-weight: 700;
        line-height: 30px;
        transform: scale(0.8);
        margin-top: 50px;
        transition: 0.5s;
        transition-delay: 0.3s;
        @media (max-width: 767px) {
          font-size: 18px;
        }
        @media (max-width: 470px) {
          font-size: 16px;
        }
      }
      strong {
        max-width: 664px;
        text-align: center;
        color: #030077;
        font-size: 34px;
        font-weight: 700;
        line-height: 50px;
        span {
          color: #d11742;
          font-size: 28px;
          display: inline-block;
          margin: auto;
        }
      }
      .nav {
        margin-top: 56px;
        position: relative;
        @media (max-width: 575px) {
          position: absolute;
          left: 30px;
          margin-bottom: 0;
          margin-top: 80px;
        }
        .left {
          max-height: 27px;
          position: absolute;
          top: 0;
          bottom: 0;
          left: -29px;
          margin: auto;
        }
        .right {
          max-height: 27px;
          position: absolute;
          top: 0;
          bottom: 0;
          right: -29px;
          margin: auto;
        }
      }
    }
  }
}
.nav {
  margin-top: 56px;
  margin-bottom: 250px;
  position: relative;
  text-align: center;
  @media (max-width: 1099px) {
    margin-bottom: 150px;
  }
  @media (max-width: 575px) {
    margin-bottom: 80px;
    margin-top: 30px;
  }
  strong {
    max-width: 664px;
    text-align: center;
    color: #030077;
    font-size: 34px;
    font-weight: 700;
    line-height: 50px;
    span {
      color: #d11742;
      font-size: 28px;
      display: inline-block;
      margin: auto;
    }
  }
  &__link {
    max-width: 60px;
    margin: 55px auto 0;
    position: relative;
  }
  .left {
    max-height: 27px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -29px;
    margin: auto;
  }
  .right {
    max-height: 27px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: -29px;
    margin: auto;
  }
}



