.header {
  background-color: $colour-blue;
  text-transform: uppercase;
  position: relative;
  z-index: 50;
  height: 80px;
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1395px !important;
    height: 100%;
  }
  nav {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 80px;
    @media (max-width: 991px) {
      display: none;
    }
  }
  a {
    color: white;
    font-size: 20px;
    font-weight: 700;
    line-height: 27px;
    text-transform: uppercase;
    white-space: nowrap;
    @media (max-width: 1200px) {
      font-size: 17px;
    }
    &:hover {
      text-decoration: none;
    }
  }
  ul {
    @media (max-width: 1200px) {
      padding-left: 5px;
    }
  }
  &__menu {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    list-style-type: none;
    align-items: center;
    margin-bottom: 0;
    height: 100%;
    li {
      height: 100%;
      padding-right: 25px;
      padding-left: 25px;
      transition: 0.3s;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      @media (max-width: 1200px) {
        padding-right: 15px;
        padding-left: 15px;
      }
      &:hover {
        cursor: pointer;
        background-color: $color-red;
      }
      &:hover .header__dropdown {
        opacity: 1;
        visibility: visible;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  &__dropdown {
    background-color: $color-red;
    position: absolute;
    list-style-type: none;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    opacity: 0;
    transition: 0.3s;
    visibility: hidden;
    padding-bottom: 22px;
    margin-left: -25px;
    top: 80px;
    width: 100%;
    @media (max-width: 1200px) {
      margin-left: -15px;
    }
    li {
      padding-top: 9px;
      padding-bottom: 9px;
      &:first-child {
        padding-top: 3px;
      }
    }
    &__inner {
      visibility: hidden;
      background-color: $colour-blue;
      position: absolute;
      left: 100%;
      top: -7px;
      padding-left: 0;
      white-space: nowrap;
      opacity: 0;
      transition: 0.3s;
      li {
        &:first-child {
          padding-top: 17px;
        }
        &:last-child {
          padding-bottom: 17px;
        }
      }
    }
    &__li {
      position: relative;
      &:hover .header__dropdown__inner {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &__social {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    @media (max-width: 991px) {
      display: flex;
      flex-direction: row;
      justify-content: center;
      position: absolute;
      bottom: 80px;
      left: 0;
      right: 0;
    }
    @media (max-height: 550px) {
      justify-content: flex-end;
      right: 20px;
      bottom: 20px;
    }
    a {
      transition: 0.3s;
      &:hover {
        margin-top: -3px;
      }
      &:last-child {
        margin-left: 9px;
      }
    }
  }
  &__mobile {
    height: 35px;
    @media (min-width: 992px) {
      display: none;
    }
    img {
      height: 100%;
    }
    &:hover {
      cursor: pointer;
    }
  }
  &__mobile-menu {
    position: fixed;
    visibility: hidden;
    background-color: $colour-blue;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: -100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    &__active {
      right: 0;
      visibility: visible;
    }
    .header__menu {
      flex-direction: column;
    }
  }
  &__mobile-container {
    display: inline-block;
    padding-left: 0;
    list-style-type: none;
    text-align: center;
    a {
      font-size: 25px;
      font-weight: 700;
    }
  }
  &__dropdown-parent {
    color: white;
    transition: 0.3s;
    display: inline-block;
    &:after {
      content: url("images/down-button.svg");
      width: 20px;
      display: inline-block;
      height: 20px;
      margin-left: 10px;
      transition: 0.3s;
    }
    &__active {
      &:after {
        transform: rotate(180deg);
      }
    }
  }
  &__mobile-dropdown {
    display: none;
    padding-left: 0;
    list-style-type: none;
    padding-top: 15px;
    padding-bottom: 15px;
    a {
      font-size: 20px;
      font-weight: 600;
    }
    &__li {
      list-style-type: none;
    }
    &__a {
      &:after {
        content: url("images/down-button.svg");
        width: 20px;
        display: inline-block;
        height: 20px;
        margin-left: 10px;
        transition: 0.3s;
      }
      &__active {
        &:after {
          transform: rotate(180deg);
        }
      }
    }
    &__inner {
      padding-top: 15px;
      padding-left: 0;
      list-style-type: none;
      display: none;
      a {
        font-size: 18px;
        font-weight: 400;
      }
    }
  }
  &__close-menu {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 30px;
    right: 30px;
    display: inline-block;
    &:hover {
      cursor: pointer;
    }
    img {
      width: 100%;
    }
  }
}



