.footer {
  padding-top: 90px;
  padding-bottom: 65px;
  h3 {
    color: $color-red;
    font-size: 20px;
    font-weight: 700;
    line-height: 12px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  p {
    color: $colour-blue;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 0;
  }
  strong {
    font-weight: 700;
  }
  &__full-container {
    max-width: 1650px;
    width: 100%;
    padding: 0 15px;
    margin: auto;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    @media (max-width: 1200px) {
      flex-wrap: wrap;
    }
    @media (max-width: 991px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
  }
  &__left {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    align-self: center;
    padding-right: 10px;
    margin-right: 80px;
    @media (max-width: 1600px) {
      margin-right: 40px;
    }
    @media (max-width: 1200px) {
      margin-right: 0;
      width: 100%;
      margin-bottom: 50px;
      justify-content: center;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      text-align: center;
    }
  }
  &__pfizer-logo {
    margin-right: 45px;
    @media (max-width: 1600px) {
      margin-right: 15px;
    }
    @media (max-width: 767px) {
      margin: 0 0 40px;
    }
  }
  &__rights {
    max-width: 330px;
    width: 100%;
    color: $colour-blue;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    @media (max-width: 1200px) {
      max-width: 800px;
    }
    @media (max-width: 767px) {
      max-width: 450px;
    }
  }
  &__column {
    max-width: 300px;
    min-width: 250px;
    padding-right: 10px;
    @media (max-width: 991px) {
      min-width: 180px;
      max-width: 500px;
      margin-bottom: 30px;
    }
    @media (max-width: 767px) {
      min-width: 100%;
      max-width: 100%;
      text-align: center;
    }
    a {
      color: $colour-blue;
      font-size: 16px;
      font-weight: 700;
      line-height: 12px;
      transition: 0.3s;
      &:hover {
        color: $color-red;
        text-decoration: none;
      }
    }
  }
  &__info {
    padding-left: 0;
    list-style-type: none;
    li {
      padding-bottom: 14px;
    }
  }
  &__links {
    ul {
      padding-left: 0;
      list-style-type: none;
      li {
        padding-bottom: 14px;
      }
    }
  }
  &__contacts {
    word-break: break-word;
  }
  &__list {
    padding-left: 0;
    list-style-type: none;
    a {
      text-transform: none;
    }
    li {
      &:before {
        content: "";
        color: $colour-blue;
      }
    }
  }
  .logo-black {
    display: none;
  }
}



