.sitemap {
  background-color: $color-grey;
  padding-top: 180px;
  padding-bottom: 430px;
  @media (max-width: 1199px) {
    padding-top: 160px;
    padding-bottom: 50px;
  }
  @media (max-width: 991px) {
    padding-top: 130px;
    padding-bottom: 40px;
  }
  @media (max-width: 767px) {
    padding-top: 100px;
    padding-bottom: 30px;
  }
  @media (max-width: 575px) {
    padding-top: 80px;
    padding-bottom: 0;
  }
  &__container {
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__inner {
    max-width: 460px;
    align-self: center;
  }
  a {
    color: $colour-blue;
    font-size: 26px;
    font-weight: 700;
    text-transform: uppercase;
    display: block;
    transition: 0.3s;
    margin-bottom: 38px;
    @media (max-width: 1199px) {
      margin-bottom: 24px;
    }
    @media (max-width: 991px) {
      margin-bottom: 16px;
    }
    @media (max-width: 767px) {
      margin-bottom: 8px;
    }
    @media (max-width: 575px) {
      margin-bottom: 0;
    }
    &:hover {
      color: $color-red;
    }
  }
  ul {
    margin-bottom: 0;
  }
  li {
    list-style-type: none;
    a {
      text-transform: none;
      &:before {
        content: "";
        color: $colour-blue;
        padding-right: 5px;
      }
    }
  }
  h1 {
    font-size: 103px;
    text-align: center;
    color: $colour-blue;
    font-weight: 900;
    margin-bottom: 100px;
    @media (max-width: 1199px) {
      font-size: 90px;
      margin-bottom: 85px;
    }
    @media (max-width: 991px) {
      font-size: 80px;
      line-height: 55px;
      margin-bottom: 70px;
    }
    @media (max-width: 767px) {
      font-size: 60px;
      margin-bottom: 65px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
      margin-bottom: 50px;
    }
  }
}



