.mini-slider {
  display: flex;
  @media (max-width: 991px) {
    flex-direction: column;
  }
  .duo-slider {
    width: 50%;
    margin-bottom: 50px;
    max-height: 600px;
    &:last-child {
      .duo-slider__mini-box {
        left: 0;
        right: auto !important;
      }
    }
    @media (max-width: 991px) {
      width: 100%;
    }
    .slick-arrow {
      display: none !important;
    }
    &__item {
      outline: none;
    }
    &__img {
      height: 300px;
      position: relative;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
    }
    &__mini-box {
      position: absolute;
      display: block;
      right: 0;
      top: 0;
      width: 103px;
      @media (max-width: 767px) {
        width: 80px;
        height: 80px;
      }
      &.red {
        background-color: #d11742;
      }
      &.blue {
        background-color: #030077;
      }
      h4 {
        text-align: center;
        line-height: 103px;
        color: #fff;
        font-size: 40px;
        font-weight: 700;
        text-transform: uppercase;
        @media (max-width: 767px) {
          color: #fff;
          font-size: 27px;
          line-height: 3;
          margin: 0;
        }
      }
    }
    &__text-box {
      max-width: 640px;
      min-height: 300px;
      margin: auto;
      padding: 0 15px;
      display: flex;
      align-items: center;
      position: relative;
      @media (max-width: 991px) {
        max-width: none;
        min-height: 200px;
        padding: 40px 15px;
      }
      @media (max-width: 575px) {
        padding: 50px 0;
      }
      p {
        color: #030077;
        font-size: 20px;
        font-weight: 400;
        line-height: 25px;
        @media (max-width: 1400px) {
          padding: 0 20px;
          font-size: 18px;
        }
      }
      .left-arrow {
        position: relative;
        padding-right: 40px;
        @media (max-width: 575px) {
          padding-right: 0;
          position: absolute;
          bottom: 0;
          left: 0;
          right: 60px;
          margin: auto;
          width: 35px;
        }
        &:before {
          content: url("images/home/left-arr.svg");
        }
      }
      .right-arrow {
        padding-left: 40px;
        @media (max-width: 575px) {
          position: absolute;
          padding-left: 0;
          bottom: 0;
          left: 0;
          right: -60px;
          margin: auto;
          width: 35px;
        }
        &:before {
          content: url("images/home/right-arr.svg");
        }
      }
    }
  }
}



