.slider {
  width: 100%;
  overflow: hidden;
  margin: 0 !important;
  .slider-box {
    &:nth-child(3) {
      outline: navajowhite;
    }
  }
  .slick-dots {
    bottom: 48px;
    @media (max-width: 575px) {
      bottom: 20px;
    }
    li {
      width: 30px;
      height: 30px;
      background: white;
      border-radius: 50%;
      @media (max-width: 575px) {
        width: 20px;
        height: 20px;
      }
      button {
        width: 16px;
        height: 16px;
        background-color: transparent;
        margin: 0;
        position: absolute;
        left: 7px;
        border-radius: 50%;
        bottom: 7px;
        opacity: 1;
        z-index: 30;
        @media (max-width: 575px) {
          width: 12px;
          height: 12px;
          left: 4px;
          bottom: 4px;
        }
        &:before {
          content: none;
        }
      }
      &.slick-active {
        button {
          background-color: #030077;
        }
      }
    }
  }
  .slick-prev {
    position: absolute;
    z-index: 9;
    left: 200px;
    top: 165px;
    bottom: 0;
    margin: auto;
    width: 60px;
    height: 60px;
    @media (max-width: 1500px) {
      left: 100px;
    }
    @media (max-width: 1200px) {
      left: 30px;
    }
    &:before {
      content: url("images/home/left-white.svg");
      position: absolute;
      left: 0;
      top: 36px;
      width: 100%;
    }
  }
  .slick-arrow {
    @media (max-width: 991px) {
      display: none !important;
    }
  }
  .slick-next {
    position: absolute;
    z-index: 9;
    right: 260px;
    top: 165px;
    bottom: 0;
    margin: auto;
    width: 60px;
    height: 60px;
    @media (max-width: 1500px) {
      right: 135px;
    }
    @media (max-width: 1200px) {
      right: 65px;
    }
    @media (max-width: 991px) {
      display: none;
    }
    &:before {
      position: absolute;
      left: 0;
      top: 36px;
      width: 100%;
      content: url("images/home/right-white.svg");
    }
  }
  .slick-track {
    .slider-box {
      &:nth-child(3) {
        background-image: none !important;
        .slider-box__img {
          margin-left: 20px;
        }
      }
    }
  }
}
.description-box {
  background-color: #030077;
  padding: 98px 0 70px;
  text-align: center;
  * {
    color: white;
  }
  h2 {
    font-size: 103px;
    font-weight: 700;
    line-height: 131px;
    @media (max-width: 1199px) {
      font-size: 90px;
    }
    @media (max-width: 991px) {
      font-size: 80px;
      line-height: 55px;
    }
    @media (max-width: 767px) {
      font-size: 60px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
    }
  }
  h4 {
    display: block;
    margin: 30px 0;
    font-size: 34px;
    font-weight: 700;
    text-transform: uppercase;
    @media (max-width: 1400px) {
      font-size: 28px;
    }
    span {
      text-transform: none;
    }
  }
  p {
    font-size: 25px;
    line-height: 40px;
    font-weight: 400;
  }
  strong {
    font-size: 25px;
    font-weight: 700;
  }
}
.parinti {
  &__title {
    max-width: 1230px;
    padding: 0 15px;
    margin: 0 auto 50px;
    text-align: center;
    h3 {
      display: block;
      margin: 82px 0 44px;
      color: #030077;
      font-size: 103px;
      font-weight: 700;
      line-height: 115px;
      @media (max-width: 1199px) {
        font-size: 90px;
      }
      @media (max-width: 991px) {
        font-size: 80px;
        line-height: 55px;
      }
      @media (max-width: 767px) {
        font-size: 60px;
      }
      @media (max-width: 575px) {
        font-size: 45px;
      }
    }
    strong {
      color: #030077;
      font-size: 28px;
      font-weight: 700;
      line-height: 40px;
      display: block;
      margin-bottom: 30px;
    }
    p {
      max-width: 985px;
      margin: auto;
      color: #030077;
      font-size: 25px;
      font-weight: 400;
      line-height: 40px;
      small {
        font-size: 15px;
        @media (max-width: 767px) {
          font-size: 9px;
        }
      }
    }
  }
  &__img {
    width: 100%;
    height: 100%;
    max-height: 945px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: relative;
    padding-bottom: 130px;
    background-position: center top;
    background-size: cover;
    @media (max-width: 767px) {
      max-height: 500px;
      padding-bottom: 50px;
      background-position: center top;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
    }
    a {
      margin: 0;
      width: 100%;
    }
    .scroll {
      max-width: 59px;
      position: absolute;
      right: 200px;
      bottom: 95px;
      @media (max-width: 991px) {
        right: 115px;
      }
      @media (max-width: 767px) {
        position: static;
        margin-top: 50px;
      }
    }
  }
}
.text-box {
  margin-top: 115px;
  max-width: 1090px;
  text-align: center;
  h3 {
    color: #030077;
    font-size: 34px;
    font-weight: 700;
    line-height: 80px;
    text-transform: uppercase;
    display: block;
    margin-top: 130px;
    margin-bottom: 30px;
    @media (max-width: 575px) {
      font-size: 31px;
      line-height: 45px;
      margin-top: 100px;
    }
  }
  p {
    color: #030077;
    font-size: 25px;
    font-weight: 400;
    line-height: 40px;
    margin-bottom: 35px;
  }
}
.people {
  max-width: 1290px;
  padding: 0 15px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 1199px) {
    flex-wrap: wrap;
  }
  &__img {
    max-width: 283px;
    border-radius: 50%;
    border: 10px solid transparent;
    align-items: center;
    margin-bottom: 25px;
    transition: 0.3s;
    img {
      width: 100%;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-height: 460px;
    @media (max-width: 1199px) {
      width: 50%;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
    &:hover {
      .people__img {
        border: 10px solid #d11742;
      }
    }
  }
  &__text {
    max-width: 311px;
    text-align: center;
    p {
      color: #030077;
      font-size: 25px;
      font-weight: 700;
      line-height: 30px;
    }
    small {
      font-size: 15px;
      font-weight: 700;
      line-height: 19px;
    }
  }
}



