body * {
  font-family: "Open Sans", sans-serif;
}
body {
  overflow-x: hidden;
  background-color: #cbd5db;
}
.full-container {
  max-width: 1395px;
  padding: 0 15px;
  margin: auto;
}

.text-container {
  max-width: 1070px;
  margin: auto;
  padding: 0 15px;
}

small {
  font-size: 15px;
  @media (max-width: 767px) {
    font-size: 9px;
  }
}

p {
  @media (max-width: 991px) {
    font-size: 21px !important;
    line-height: 29px !important;
  }
  @media (max-width: 575px) {
    font-size: 18px !important;
    line-height: 29px !important;
  }
}

strong {
  @media (max-width: 991px) {
    font-size: 21px !important;
    line-height: 29px !important;
  }
  @media (max-width: 575px) {
    font-size: 18px !important;
    line-height: 29px !important;
  }
}

a {
  outline: none;
  &:hover {
    text-decoration: none;
  }
}

.tabs {
  margin-top: 130px;
  @media (max-width: 991px) {
    margin-top: 80px;
  }
  .nav {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding: 0;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    &-item {
      width: 33.3%;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 767px) {
        width: 100%;
      }
      a {
        width: 100%;
        &.active {
          background-color: #d11742;
        }
        height: 80px;
        background-color: #030077;
        color: #fff !important;
        font-size: 35px;
        font-weight: 700;
        line-height: 12px;
        text-transform: uppercase;
        @include flex-center;
        @media (max-width: 1099px) {
          font-size: 28px;
        }
        @media (max-width: 991px) {
          font-size: 24px;
        }
      }
    }
  }
  &__text {
    margin-top: 110px;
    margin-bottom: 90px;
    text-align: center;
    @media (max-width: 575px) {
      margin-top: 65px;
      margin-bottom: 60px;
    }
    h3 {
      padding: 0 15px;
      color: #030077;
      font-size: 103px;
      font-weight: 700;
      line-height: 120px;
      display: block;
      margin-bottom: 50px;
      @media (max-width: 1199px) {
        font-size: 90px;
      }
      @media (max-width: 991px) {
        font-size: 80px;
        line-height: 55px;
      }
      @media (max-width: 767px) {
        font-size: 60px;
      }
      @media (max-width: 575px) {
        font-size: 45px;
      }
    }
    p {
      font-size: 25px;
      line-height: 40px;
      color: #030077;
      font-weight: 400;
    }
    strong {
      color: #d11742;
      font-size: 25px;
      font-weight: 700;
      line-height: 40px;
    }
  }
  &__element {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 150px;
    @media (max-width: 991px) {
      margin-bottom: 100px;
      justify-content: center;
    }
  }
  .simp-element {
    max-width: 262px;
    width: 100%;
    margin: 15px 0;
    min-height: 235px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    &:first-child {
      margin-left: 0;
    }
    &:nth-child(4n + 4) {
      margin-right: 0;
    }
    &:nth-child(5) {
      margin-left: 0;
    }
    &:nth-child(9) {
      margin-left: 0;
    }
    &__img {
      position: relative;
      display: block;
      max-width: 163px;
      margin-bottom: 25px;
      img {
        width: 100%;
        transition: 0.1s;
      }
      .gif {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0;
      }
      &:hover {
        .static {
          opacity: 0;
        }
        .gif {
          opacity: 1;
        }
      }
    }
    &__text {
      text-align: center;
      p {
        color: #030077;
        font-size: 25px;
        font-weight: 700;
        line-height: 37px;
      }
    }
  }
  .tab-content {
    outline: none;
    position: relative;
    padding-bottom: 130px;
    .tab-pane {
      &.fade {
        display: none;
        visibility: hidden;
      }
      &.active {
        visibility: visible;
        display: block;
        position: relative !important;
        top: 0;
      }
    }
  }
}

.slick-arrow {
  outline: none;
  &:before {
    opacity: 1 !important;
  }
}
.fill-white {
  color: white !important;
  background-color: white !important;
  fill: white;
}

.nicescroll-rails {
  background-color: rgb(51, 122, 183);
  width: 3px !important;
}

.title {
  margin-top: 150px;
  text-align: center;
  h2 {
    color: #030077;
    font-size: 103px;
    font-weight: 700;
    line-height: 122px;
    @media (max-width: 1199px) {
      font-size: 90px;
    }
    @media (max-width: 991px) {
      font-size: 80px;
      line-height: 55px;
    }
    @media (max-width: 767px) {
      font-size: 60px;
    }
    @media (max-width: 575px) {
      font-size: 45px;
    }
  }
}

.bg-image {
  overflow: hidden;
  max-height: 705px;
  @media (max-width: 767px) {
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
  .full-img {
    width: 100%;
    height: auto;
    transition: 0.9s;
    @media (max-width: 767px) {
      display: none;
    }
    &:hover {
      transform: scale(1.1);
    }
  }
}



