.protezi {
  background-color: #030077;
  padding: 100px 0 130px;
  @media (max-width: 575px) {
    padding: 100px 0 78px;
  }
  .container {
    max-width: 1149px;
  }
  &__title {
    text-align: center;
    margin-bottom: 200px;
    @media (max-width: 1199px) {
      margin-bottom: 120px;
    }
    h2 {
      color: #fff;
      font-size: 103px;
      font-weight: 700;
      line-height: 129px;
      display: block;
      margin-bottom: 32px;
      @media (max-width: 1199px) {
        font-size: 90px;
      }
      @media (max-width: 991px) {
        font-size: 80px;
        line-height: 55px;
      }
      @media (max-width: 767px) {
        font-size: 60px;
      }
      @media (max-width: 575px) {
        font-size: 45px;
      }
    }
    h4 {
      color: #fff;
      font-size: 34px;
      font-weight: 700;
      line-height: 37px;
      text-transform: uppercase;
      @media (max-width: 767px) {
        font-size: 24px;
        line-height: 33px;
      }
    }
  }
  &__all {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 230px;
    @media (max-width: 1199px) {
      margin-bottom: 100px;
    }
    @media (max-width: 991px) {
      flex-direction: column;
      justify-content: center;
    }
    @media (max-width: 575px) {
      margin-bottom: 50px;
    }
  }
  &__item {
    max-width: 343px;
    flex-direction: column;
    @include flex-center;
    &:nth-child(2) {
      @media (max-width: 1199px) {
        margin: 0 50px;
      }
    }
    @media (max-width: 991px) {
      margin: 0 0 50px !important;
    }
  }
  &__img {
    margin-bottom: 40px;
    position: relative;
    img {
      width: 100%;
      transition: 0.1s;
    }
    .gif {
      opacity: 0;
      position: absolute;
      left: 0;
      top: 0;
    }
    &:hover {
      .static {
        opacity: 0;
      }
      .gif {
        opacity: 1;
      }
    }
  }
  &__text {
    text-align: center;
    strong {
      color: #fff;
      font-size: 25px;
      font-weight: 700;
      line-height: 30px;
    }
  }
}



