.afectat,
.conse {
  .footer {
    background: white;
    .logo-white {
      display: none;
    }
    .logo-black {
      display: block;
    }
  }
  .fixed-box {
    margin-bottom: 150px;
  }
  .semne {
    text-align: center;
    h4 {
      color: #030077;
      font-size: 70px;
      font-weight: 700;
      text-transform: uppercase;
      @media (max-width: 991px) {
        font-size: 60px;
      }
      @media (max-width: 768px) {
        font-size: 50px;
      }
      @media (max-width: 575px) {
        font-size: 27px;
      }
    }
    span {
      font-size: 41px;
      color: #030077;
      font-weight: 700;
      text-transform: uppercase;
      @media (max-width: 991px) {
        font-size: 31px;
      }
      @media (max-width: 768px) {
        font-size: 21px;
      }
    }
  }
  .tabs__element {
    width: 100%;
    justify-content: center;
  }
  .simp-element {
    max-width: 260px;
    margin: 0 20px;
    &__img {
      max-height: 160px;
    }
    &__text {
      max-width: 260px;
      margin: 0 20px;
    }
  }
}



