.fixed-box {
  max-width: 900px;
  height: 659px;
  margin: auto;
  position: relative;
  .arr {
    display: none;
    background-image: url("images/drag.svg");
    position: absolute;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    transform: rotate(90deg);
    right: 6px;
    bottom: 0;
    transition: 0.2s;
    animation-name: example;
    animation-iteration-count: infinite;
    animation-duration: 1s;
    @media (max-width: 991px) {
      display: block;
    }
  }
  @keyframes example {
    from {
      bottom: 0;
    }
    to {
      bottom: 100px;
    }
  }
  @media (max-width: 991px) {
    margin-bottom: 60px !important;
    height: auto;
  }
}
.scroll-boxes {
  position: relative;
  width: 100%;
  max-width: 900px;
  height: 659px;
  overflow: scroll;
  overflow-x: hidden;
  margin: 200px auto 300px;
  padding: 0 15px;
  @media (max-width: 991px) {
    line-height: 34px;
    margin: 30px auto 50px;
    overflow: hidden;
    outline: none;
    max-height: 345px;
  }
  small {
    font-size: 15px;
    font-weight: 700;
  }
  ul {
    li {
      color: #030077;
      font-size: 25px;
      font-weight: 400;
      line-height: 37px;
      margin-bottom: 50px;
      @media (max-width: 991px) {
        font-size: 21px !important;
        line-height: 29px !important;
        margin-bottom: 15px;
      }
      @media (max-width: 575px) {
        font-size: 18px !important;
        line-height: 29px !important;
      }
    }
  }
  strong {
    color: #030077;
    font-size: 28px;
    font-weight: 700;
    display: block;
    margin-bottom: 80px;
    text-transform: uppercase;
    @media (max-width: 991px) {
      margin-bottom: 50px;
    }
    span {
      color: #d11742;
      font-size: 28px;
    }
  }
  a {
    color: #030077 !important;
  }
  p {
    width: 100%;
    color: #030077;
    font-size: 25px;
    font-weight: 400;
    line-height: 37px;
    display: block;
    margin-bottom: 50px;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 30px;
      margin-bottom: 25px;
    }
    small {
      font-weight: 400;
    }
  }
}

.counter {
  position: absolute;
  top: 0;
  line-height: 69px;
  bottom: 0;
  right: -92px;
  @media (max-width: 1199px) {
    display: none;
  }
  span {
    color: #030077;
    font-size: 28px;
    font-weight: 700;
    line-height: 50px;
  }
}

.nicescroll-rails {
  background-color: #030077 !important;
}

.nicescroll-cursors {
  background-color: #030077 !important;
  left: 4px !important;
  border: none !important;
}



