.cum {
  .title-page {
    h2 {
      display: block;
      text-align: left;
      margin-top: 130px;
      margin-left: 175px;
      @media (max-width: 991px) {
        margin: 0;
        text-align: center !important;
      }
    }
  }
  .nav-bottom {
    margin-top: 550px;
    margin-bottom: 70px;
    @media (max-width: 991px) {
      margin-top: 40px;
      margin-bottom: 100px;
    }
  }
  .scroll-boxes {
    margin: 125px auto 300px;
    @media (max-width: 991px) {
      margin: 0 auto;
    }
  }
  .description {
    max-width: 1440px;
    margin: 0 auto 110px;
    padding: 0 15px;
    text-align: center;
    p {
      color: #030077;
      font-family: "Helvetica Neue", sans-serif;
      font-size: 20px;
      font-weight: 400;
    }
  }
  .footer {
    background: white;
    .logo-white {
      display: none;
    }
    .logo-black {
      display: block;
    }
  }
}



